import { Component, Element, h } from '@stencil/core'

@Component({
  tag: 'ato-row',
  shadow: false,
})
export class AtoRow {
  @Element() private element: HTMLElement

  componentDidLoad() {
    this.element.outerHTML = this.element.outerHTML.replace(/ato/g, 'ion')
  }

  render() {
    return <slot />
  }
}
